import React from 'react';
import {CheckboxGroup} from '@here/hds-checkbox';
// eslint-disable-next-line no-new
new CheckboxGroup();

interface HDSCheckboxProperties extends React.HTMLAttributes<HTMLElement> {
  children: React.ReactNode;
  label: string | undefined;
  secondaryLabel?: string | undefined;
  ariaLabel?: string | undefined;
}
const HDSCheckboxGroup = React.forwardRef(
  (
    {children, label, secondaryLabel, ariaLabel, className, ...rest}: HDSCheckboxProperties,
    ref
  ) => (
    <hds-checkbox-group
      ref={ref}
      {...(label && {label})}
      {...(secondaryLabel && {'secondary-label': secondaryLabel})}
      {...(className && {class: className})}
      {...(ariaLabel && {'aria-label': ariaLabel})}
      {...rest}
    >
      {children}
    </hds-checkbox-group>
  )
);

HDSCheckboxGroup.defaultProps = {
  label: '',
  secondaryLabel: '',
  ariaLabel: '',
};

export default HDSCheckboxGroup;
